@mixin t-anim--transition($properties, $time: $s-anim--time-short, $ease: $s-anim--ease-out, $delay: '') {
  transition-timing-function: $ease;
  transition-duration: $time;
  transition-property: $properties;
  @if $delay != '' {
    transition-delay: $delay;
  }
}

@mixin t-anim--animation(
  $name: '',
  $time: $s-anim--time-short,
  $ease: $s-anim--ease-out,
  $delay: '',
  $fillMode: '',
  $iterationCount: infinite
) {
  animation-duration: $time;
  animation-timing-function: $ease;
  animation-iteration-count: $iterationCount;
  
  @if $fillMode != '' {
    animation-fill-mode: $fillMode;
  }

  @if $delay != '' {
    animation-delay: $delay;
  }

  @if $name != '' {
    animation-name: $name;
  }
}


@mixin t-anim--background-swipe($color){
  @include t-media-query($s-small-up) {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      height: 100%;
      left: 0;
      transition: width $s-anim--time-med;
      width: 0;
      display: block;
      content: "";
      background-color: $s-color--background-octonary;
      z-index: -1;
    }

    &:hover::before{
      width: 100%;
    }
  }

}


.has-fade-up{
  opacity: 0;
  transform: translateY(t-to-rem(20));
  transition: all 3s;

  &.fade-up{
    opacity: 1;
    transform: translateX(0);
  }
}