@import '../base.scss';

.c-featured-text {
  padding: 1.25rem 2.375rem 2.75rem;
  // padding: 1.25rem 2.375rem 0;


  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background: rgba($s-color--blue-green, 0.2);
    margin-bottom: 4.25rem;
  }

  &__subtitle {
    padding-bottom: 0.625rem;
  }

  &__title {
    padding-bottom: 0.875rem;
  }

  &__text {
    max-width: 38.75rem;
  }

  @include t-media-query($s-small-up) {
    max-width: 60rem;
    margin: auto;
    
    &::before { display: none; }

    &__subtitle {
      padding-bottom: 1rem;
    }

    &__title {
      padding-bottom: 1.25rem;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 7.5rem 2rem 10rem;
    // padding: 7.5rem 2rem 0;
  }
}